body {
  font-family: sans-serif;
  background-color: rgb(5, 5, 10);
  color: rgb(255, 165, 0);
}
#startScreen {
  background-image: url("./assets/background.png");
  background-size: 810px 610px;
  position: absolute;
  z-index: 5;
  width: 810px;
  height: 610px;
  margin: 0 auto;
}
#startScreen::before {
  content: "";
  position: absolute;
  width: 810px;
  height: 610px;
  margin: 0 auto;
  background-image: linear-gradient(
    to bottom,
    rgba(12, 12, 23, 1),
    rgb(5, 5, 10, 1) 70%
  );
  opacity: 0.9;
}
#game {
  background-image: url("./assets/background21.png");
  background-size: 810px 610px;
  display: hidden;
}

#startText {
  color: rgb(255, 165, 0);
  top: 85%;
  text-align: center;
  position: absolute;
  margin-top: -25px;
  width: 100%;
  height: 100%;

  animation-name: textFlash;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
#penis {
  color: rgb(238, 255, 0);
  animation-name: glitch;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.deathText {
  top: 85%;
  text-align: center;
  padding-left: 100px;
  padding-right: 100px;
  overflow: hidden;
  transform: translate(2000px, 0px);
  transition: transform 2s ease-in-out;
  filter: drop-shadow(0px 1px 0px rgb(219, 146, 9))
    drop-shadow(0px 1px 0px rgb(185, 123, 8))
    drop-shadow(0px 1px 0px rgb(141, 94, 6))
    drop-shadow(0px 1px 0px rgb(119, 79, 5))
    drop-shadow(0px 1px 0px rgb(65, 42, 0));
}
#darkness {
  position: absolute;
  z-index: 2;
  margin: 0 auto;
  width: 810px;
  overflow: hidden;
  height: 610px;
  background-image: linear-gradient(
    to bottom,
    rgb(11, 37, 41),
    rgb(22, 2, 2) 70%
  );
}
.Title {
  color: rgb(238, 255, 0);
  top: 45%;
  text-align: center;
  position: absolute;
  margin-top: -25px;
  transform: perspective(60px) scaleY(3) rotateX(-20deg);
  filter: drop-shadow(0px 2px 0px rgb(219, 146, 9))
    drop-shadow(0px 2px 0px rgb(185, 123, 8))
    drop-shadow(0px 2px 0px rgb(141, 94, 6))
    drop-shadow(0px 2px 0px rgb(119, 79, 5))
    drop-shadow(0px 2px 0px rgb(65, 42, 0));
  margin-left: 150px;
  margin-right: 150px;
}

#score {
  margin-right: 100px;
}
#game {
  border-style: solid;
  border-radius: 4px;
  border-color: rgb(255, 165, 0);
}
#Score {
  color: rgb(255, 165, 0);
  text-align: left;
}
#Audio {
  color: rgb(255, 165, 0);
  float: right;
  text-align: right;
}
#fpsCounter {
  position: relative;
  z-index: 4000;
}
#fpsCounter::after {
  content: " fps per frame";
}
#announcements {
  text-align: center;
  position: absolute;
  top: 450px;
  margin: 0 auto;
  left: 200px;
  width: 400px;
}
#announcements h2 {
  transform: perspective(60px) scaleY(3) rotateX(-10deg);
  animation-name: announcements;
  animation-duration: 1s;
  animation-timing-function: both;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  filter: drop-shadow(0px 1px 0px rgb(219, 146, 9))
    drop-shadow(0px 1px 0px rgb(185, 123, 8))
    drop-shadow(0px 1px 0px rgb(141, 94, 6))
    drop-shadow(0px 1px 0px rgb(119, 79, 5))
    drop-shadow(0px 1px 0px rgb(65, 42, 0));
}
hr {
  width: 50px;
  border-color: transparent;
  border-right-color: rgba(255, 165, 0, 0.5);
  border-right-width: 100px;
  position: absolute;
  bottom: 100%;

  transform-origin: 100% 50%;
  animation-name: rain;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
#rain {
  z-index: 5;
  width: 800px;
  height: 600px;
  position: absolute;
  display: inline;
  overflow: hidden;
}
@keyframes rain {
  from {
    transform: rotate(105deg) translateX(0);
  }
  to {
    transform: rotate(105deg) translateX(calc(100vh + 20px));
  }
}
@keyframes announcements {
  from {
    transform: perspective(100px) scaleY(1) rotateY(4deg) rotateX(-4deg);
  }
  to {
    transform: perspective(150px) scaleY(1) rotateY(-14deg) rotateX(4deg);
  }
}
@keyframes textFlash {
  from {
    color: rgb(255, 165, 12);
    filter: blur(0px) drop-shadow(0px 0px 5px rgb(255, 208, 119));
  }
  to {
    color: rgb(255, 255, 12);
    filter: blur(0.5px) drop-shadow(0px 0px 25px rgb(255, 165, 0));
  }
}
@keyframes glitch {
  0% {
    color: rgb(255, 255, 12);
  }
  78% {
    color: rgb(255, 255, 12);
  }
  80% {
    color: rgb(215, 195, 120);
  }
  81% {
    color: rgb(255, 255, 12);
  }
  100% {
    color: rgb(255, 255, 12);
  }
}
